<template>
  <v-app id="inspire">
    <v-navigation-drawer
        app
        style="background: #E5F0F4"
    >
      <v-list-item style="background: #1BAFDB">
        <v-list-item-content style="background: #1BAFDB">
          <v-list-item-title class="text-h6" style="background: #1BAFDB; height: 21px">
            <v-icon style="color: #E5F0F4;"  @click="goHome">mdi-home</v-icon>
          </v-list-item-title>
          <v-list-item-subtitle>
            <br>
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <SideBarComponent :dataDashboard="dataDashboard" @selectName="selectName"></SideBarComponent>
    </v-navigation-drawer>

    <v-app-bar app style="background: #1BAFDB">

      <v-spacer></v-spacer>
      <div>
        <v-btn icon >
          <v-icon style="color: #E5F0F4">mdi-bell</v-icon>
        </v-btn>
        <v-menu
            left
            bottom
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
                icon
                v-bind="attrs"
                v-on="on"
            >
              <v-avatar>
                <img
                    src="https://cdn.vuetifyjs.com/images/john.jpg"
                    alt="logo"
                >
              </v-avatar>
            </v-btn>
          </template>

          <v-list>
            <v-list-item
            >
              <v-list-item-title @click="logOut" >Salir </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
        <v-btn icon>
        </v-btn>
        <v-btn icon>
        </v-btn>
      </div>

    </v-app-bar>


      <slot name="slot-pages"></slot>

  </v-app>
</template>

<script>
import SideBarComponent from "@/components/dashboard/SideBarComponent";
export default {
name:"Dashboardcomponent",
  components: {
    SideBarComponent
  },
  props: ['dataDashboard'],
  data () {
    return {
    }
  },
  methods: {
    goHome () {
      this.$router.push({ path: '/menu' })
    },
    selectName (item) {
      this.$emit('changeView', item);
    },
    logOut () {
      this.$router.push({ path: '/' })
    },


  },
}
</script>